body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.4;
    height: 3000px;
    background-color: #000;
}

a {
    text-decoration: none;
    /* color:#222; */
}

img {
    display: block;
}


.inner {
    position: relative;
    width: 1100px;
    margin: auto;

}

.headerWrap {
    background-color: #000;
    position: relative;
    width: 1100px;
    height: 160px;
    display: flex;
}

.headerWrap .logo {
    position: relative;
    width: 102px;
    height: 80px;
    cursor: pointer;
}

.headerWrap .logo img {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 118px;
}

.headerWrap .header_right_wrap {
    position: absolute;
    width: 350px;
    height: 80px;
    right: 0;
}

.headerWrap .header_right_wrap .header_right {
    position: absolute;
    display: flex;
    height: 42px;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}

.headerWrap .header_right_wrap .header_right .header_right_host {
    display: flex;
    margin-top: 10px;
}

.headerWrap .header_right_wrap .header_right .header_right_host .host a {
    font-size: 14px;
    font-weight: bold;
    padding: 11px 20px;
    border-radius: 100px;
    color: #fff;
}

.headerWrap .header_right_wrap .header_right .header_right_host .host a:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.headerWrap .header_right_wrap .header_right .header_right_host .language {
    position: relative;
    margin: -9px auto;
    bottom: 0;
    width: 40px;
    height: 40px;
    border-radius: 100px;
}

.headerWrap .header_right_wrap .header_right .header_right_host .language:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.headerWrap .header_right_wrap .header_right .header_right_host .language .material-icons {
    position: absolute;
    color: #fff;
    top: 10px;
    left: 10px;
    font-size: 20px;
}

.headerWrap .header_right_wrap .header_right .signin {
    background-color: #fff;
    width: 77px;
    height: 42px;
    border: 1px solid #929292;
    border-radius: 50px;
    box-sizing: border-box;
    margin-left: 10px;
    cursor: pointer;
}

.headerWrap .header_right_wrap .header_right .signin .signin_item {
    position: absolute;
    height: 40px;
    display: flex;
    top: 12px;
    bottom: 0;
    right: -22px;
    margin: auto;
}

.headerWrap .header_right_wrap .header_right .signin_item .material-icons:first-child {
    padding-right: 5px;
    margin-top: 2.5px;
}

.headerWrap .header_right_wrap .header_right .signin_item .material-icons:nth-child(2) {
    color: #696969;
    font-size: 30px;
}



/* 헤더 중앙 메뉴 */
.menu_inner {
    position: absolute;
    width: 1100px;

}

.menu_inner .menu {
    position: absolute;
    width: 300px;
    height: 80px;
    top: 0;
    bottom: 0;
    left: calc(1100px / 3 + 50px);

}

.menu_inner .menu .item {
    position: absolute;
    top: 0;
    bottom: 0;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 300px;
    height: 48px;
    display: flex;
    position: absolute;

}

.menu .item .item__name {
    margin-top: 15px;
    padding: 10px 16px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
}

.menu .item .item__name a {
    color: #fff;
}

.menu .item .item__name a:hover {
    color: #929292;
}

.menu .item .item__name:hover {
    color: #929292;
}

.menu .item .item__name::after {
    content: "";
    width: 0;
    height: 2px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, .5);
    margin: auto;
    display: block;
    margin-top: 7px;
    transition: width .1s;
}

.menu .item .item__name:hover::after {
    width: 5px;
}


/* 메뉴 클릭시 on off */
.menu .item .item__name.first_name.on::after {
    content: "";
    width: 17px;
    height: 2px;
    border-radius: 10px;
    background-color: #fff;
    margin: auto;
    display: block;
    margin-top: 7px;
}

.menu .item .item__name.first_name.on:hover {
    color: #fff;
}


.menu .item .item__name.second_name.on::after {
    content: "";
    width: 17px;
    height: 2px;
    border-radius: 10px;
    background-color: #fff;
    margin: auto;
    display: block;
    margin-top: 7px;
}

.menu .item .item__name.second_name.on:hover {
    color: #fff;
}



/* 메뉴 클릭이벤트 */
.menu .item .item__contents.first.hide {
    display: none;
}

.menu .item .item__contents.second.hide {
    display: none;
}



.menu .item .item__contents.first {
    position: absolute;
    top: 70px;
    width: 1100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu .item .item__contents.second {
    display: none;
    position: absolute;
    top: 70px;
    width: 1100px;
    justify-content: center;
    align-items: center;
}

.menu .item .item__contents.second.on {
    display: flex;
    position: absolute;
    top: 70px;
    width: 1100px;
    justify-content: center;
    align-items: center;
}



.menu .item .item__contents .contents__menu {
    position: relative;
    display: flex;
    border: 0px solid transparent;
    border-radius: 200px;
    box-sizing: border-box;
    background-color: #fff;
    margin-right: 20px;
    height: 64px;
    width: 860px;
}

.menu .item .item__contents .contents__menu>li:nth-of-type(1) {
    position: relative;
    flex-grow: 1;
    border-radius: 200px;
    box-sizing: border-box;
    padding-left: 35px !important;
    padding-right: 70px;

}

.menu .item .item__contents .contents__menu>li:nth-of-type(2),
.menu .item .item__contents .contents__menu>li:nth-of-type(3) {
    padding-right: 80px;
}

.menu .item .item__contents .contents__menu>li {
    position: relative;
    flex-grow: 1;
    border-radius: 200px;
    box-sizing: border-box;
    padding: 15px 100px 10px 20px;

}

.menu .item .item__contents .contents__menu>li.on {
    position: relative;
    flex-grow: 1;
    border-radius: 200px;
    box-sizing: border-box;
    padding: 15px 100px 10px 20px;
    box-shadow: rgba(65, 3, 63, 0.15);
}

.menu .item .item__contents .contents__menu>li:hover {
    background-color: rgba(0, 0, 0, .05);
    cursor: pointer;
}

.menu .item .item__contents .contents__menu>li::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0px;
    margin: auto;
    width: 1px;
    height: 25px;
    background-color: rgba(0, 0, 0, .1);
}

.menu .item .item__contents .contents__menu>li:last-child::before {
    display: none;
}

.menu .item .item__contents .contents__menu li ul.contents {
    position: relative;
    height: 40px;
}

.menu .item .item__contents .contents__menu li ul.contents {
    display: flex;
}

.menu .item .item__contents .search_icon {
    position: absolute;
    width: 47px;
    height: 47px;
    right: 140px;
    background-color: #ff385c;
    border-radius: 50px;
    cursor: pointer;
}

.menu .item .item__contents .search_icon:hover {
    background-color: #f0264b;
}

.menu .item .item__contents.first .search_icon.on,
.menu .item .item__contents.second .search_icon.on {
    position: absolute;
    width: 90px;
    height: 47px;
    right: 140px;
    background-color: #f0264b;
    border-radius: 50px;
    cursor: pointer;
}

.menu .item .item__contents .search_icon.on:hover {
    background-color: #f0264b;
}

.menu .item .item__contents .search_icon .material-icons {
    position: absolute;
    color: #fff;
    font-size: 22px;
    top: 13px;
    left: 13px;
}

.menu .item .item__contents .contents__menu li ul.contents li input {
    height: inherit;
    outline: none;
    background-color: transparent;
    border: 0px solid transparent;
    color: #222;
    width: 125px;
}

.menu .item .item__contents .contents__menu li ul.contents li ul li:nth-child(1) {
    font-size: 12px;
    font-weight: bold;
}

.menu .item .item__contents .contents__menu li ul.contents li ul li:nth-child(2) {
    font-size: 14px;
    color: #929292
}



/* 메뉴에서 옵션으로 뜨는 창 */
/* 위치 */
.menu_inner .location_search {
    position: absolute;
    top: 160px;
    left: 130px;
    width: 400px;
    height: 166px;
    border-radius: 30px;
    background-color: #fff;
    box-shadow: 0px 0px 20px rgba(65, 3, 63, 0.15);
}

.menu_inner .location_search>ul {
    margin-top: 24px;
    margin-left: 32px;
}

.menu_inner .location_search li.location_search_txt {
    padding: 8px 0;
    font-size: 14px;
    font-weight: bold;
}

.menu_inner .location_search li.location_search_button {
    position: relative;
    top:16px;
    height: 58px;
    width: 336px;
    border: 1px solid #cccccc;
    border-radius: 100px;
    cursor: pointer;
}
.menu_inner .location_search li.location_search_button:hover{
    box-shadow: 0px 0px 20px rgba(65, 3, 63, 0.15);
}
.menu_inner .location_search li.location_search_button ul{
    position: absolute;
    width: calc(336px - (24px * 2));
    height: calc(58px - (16px * 2));
    top: 16px;
    left: 24px;
}
.menu_inner .location_search li.location_search_button ul>li {
    position:absolute;
    color: #61045F;
    font-weight: bold;
    font-size: 18px;
}
.menu_inner .location_search li.location_search_button ul>li.arrow_button{
    right:0;
}

.menu_inner .location_search li.location_search_button ul li.material-icons {
    background-color:orange;
}

